import { API_BASE_URL } from '../../../../common/constants/constants';
import { initGA } from '../../../initGA';
import { http } from '../../../services';
import { saveToken } from '../../../services/http/storage';

const isElectron = !!window.require;

export async function logIn(username: string, password: string) {
  const googleClientId = await initGA();

  const requestBody = JSON.stringify({
    fromApp: isElectron,
    googleClientId,
    password,
    username,
  });

  const loggedInAccount = await http(`${API_BASE_URL}/user/login`, {
    method: 'POST',
    body: requestBody,
  });

  saveToken(loggedInAccount?.body?.token, 'logIn');

  await initGA();

  return loggedInAccount;
}
