import React, { memo, useEffect, useRef, useState } from 'react';
import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';

import { history } from '../../../../services';
import { isEmailValid } from '../../../../utils/email-validator';
import { checkAuthentication } from '../../helpers';
import OAuth from '../../o-auth';
import {
  AuthContentContainer,
  AuthTextField,
  Form,
  NavigationContainer,
  NavigationDividerContainer,
  NavigationLink,
  PageWrapper,
  SubmitButton,
  Title,
} from '../../StyledComponents';
import { logIn } from '../api';

type TErrorInputs = 'email'|'password';

const isElectron = !!window.require;

const SignIn = () => {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [errorInputs, setErrorInputs] = useState<TErrorInputs[]>([]);

  const { t: translation } = useTranslation();

  const emailFieldRef = useRef() as React.MutableRefObject<HTMLInputElement>;
  const passwordFieldRef = useRef() as React.MutableRefObject<HTMLInputElement>;

  useEffect(() => {
    checkAuthentication();
  }, []);

  const onLoginSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const email = emailFieldRef.current.value;
    const password = passwordFieldRef.current.value;

    // Validating email
    const isEmailValidationSuccess = isEmailValid(email);
    if (!isEmailValidationSuccess) {
      emailFieldRef.current.focus();
      setErrorInputs(['email']);

      return null;
    }

    try {
      setIsLoading(true);

      await logIn(email, password);

      history.replace('/profileList');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <PageWrapper>
      <AuthContentContainer>
        <Title>
          {translation('auth.login.title')}
        </Title>

        <OAuth.Buttons isElectron={isElectron} />

        <Form onSubmit={onLoginSubmit}>
          <AuthTextField
            ref={emailFieldRef}
            status={errorInputs.includes('email') ? 'error' : 'default'}
            placeholder={translation('auth.login.form.emailInputPlaceholder')}
            type='email'
            required={true}
            autoFocus={true}
          />
          <AuthTextField
            ref={passwordFieldRef}
            status={errorInputs.includes('email') ? 'error' : 'default'}
            placeholder={translation('auth.login.form.passwordInputPlaceholder')}
            type='password'
            required={true}
          />

          <SubmitButton type='submit' loading={isLoading}>
            {translation('auth.login.form.submitBtnTxt')}
          </SubmitButton>
        </Form>

        <NavigationContainer>
          <NavigationLink to='/sign_up'>
            {translation('auth.login.links.signUp')}
          </NavigationLink>
          <NavigationDividerContainer>
            â€¢
          </NavigationDividerContainer>
          <NavigationLink to='/forgot_password'>
            {translation('auth.login.links.forgotPassword')}
          </NavigationLink>
        </NavigationContainer>
      </AuthContentContainer>

      <Helmet>
        <title>
          {translation('auth.login.windowTitle')}
          {' '}
          - GoLogin
          {' '}
          {window.gologinAppVersion || ''}
        </title>
      </Helmet>
    </PageWrapper>
  );
};

export default memo(SignIn);
